<template>
    <div class="bindEmail">
        <div v-if="data.isset === 0" class="email-form">
            <p class="title">{{$L('绑定邮箱后，您可以使用邮箱快捷找回密码')}}</p>
            <el-form class="phone-box">
                <el-input v-model="newEmail" size="medium" :placeholder="$L('输入新邮箱')"></el-input>
                <el-input v-model="emailCode" size="medium" :placeholder="$L('输入邮箱验证码')">
                    <el-button @click="getEmailMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>

                    <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>
                </el-input>
                <el-button type="primary" @click="bindFunc">{{ $L(' 绑定') }}</el-button>
            </el-form>
        </div>

        <div v-else-if="data.isset === 1" class="email-form">
            <div style="margin-top: 30px" v-if="pass === 1">
                <p class="title">{{$L('当前邮箱')}} ：{{ data.emailNum }}</p>
                <el-form class="phone-box">
                    <el-input size="medium" v-model="newEmail" :placeholder="$L('输入新邮箱')"></el-input>
                    <el-input v-model="emailCode" size="medium" :placeholder="$L('输入邮箱验证码')">
                        <el-button @click="getEmailMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>

                        <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>
                    </el-input>
                    <el-button type="primary" @click="bindFunc">{{ $L('提交') }}</el-button>
                </el-form>
            </div>

            <div class="safetyCheck" v-else-if="pass === 0">
                <div class="phone-form">
                    <el-tabs v-model="activeName">
                        <el-tab-pane :label="$L('密码验证')" name="pwd">
                            <div class="phone-box">
                                <el-input size="medium" v-model="password" show-password :placeholder="$L('输入登录密码')"></el-input>
                                <el-button type="primary" @click="pwdCheck">{{ $L('验证') }}</el-button>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane :label="$L('手机验证')" name="phone" v-if="data.issetMobile === 1 && data.mobileNum">
                            <p class="title">{{$L('当前手机号')}}：{{ data.mobileNum }}</p>
                            <div class="phone-box">
                                <el-input size="medium" v-model="mobileCode" :placeholder="$L('输入手机验证码')">
                                    <el-button @click="getPhoneMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>
                                    <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>
                                </el-input>
                                <el-button type="primary" @click="phoneCheck">{{ $L('验证') }}</el-button>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "bindEmail",
        inject: ["reload"],
        props: {
            data: {
                type:Object,
                default: {}
            }
        },
        data(){
            return{
                isBind:1,
                pass:0,
                newEmail:'',
                emailCode:'',
                mobileCode:'',
                password:'',
                message:'',
                activeName:'pwd',
                type:'phone',
                isPwd:0,
                phoneNumber:15548692123,
                show: true,
                count: '',
                timer: null,
                tableData: {},
                emailNum: '',

            }
        },
        created() {

        },
        methods:{
            getPhoneMessage(){

                if (this.data.trueMobileNum !== '') {
                    let reg = /^1[0-9]{10}$/;
                    if (!reg.test(this.data.trueMobileNum)) {
                        this.$message.error(this.$L("手机号码不正确"));
                        return;
                    }
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    send_mobile: 1
                };
                _self.$api('sendCodeChange', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;

                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        console.log("false:", _self.show);

                        _self.$message({
                            message: _self.$L('手机验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$emit("closeDialog");
                    });
                });
            },
            getEmailMessage(){

                if (this.newEmail.trim() === '') {
                    this.$message.error(this.$L("请输入邮箱"));
                    return;
                } else {
                    if (this.newEmail.trim() !== '') {
                        let reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
                        if (!reg.test(this.newEmail.trim())) {
                            this.$message.error(this.$L("请输入有效的邮箱"));
                            return;
                        }
                    }

                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    email:　_self.newEmail,
                    send_email: 1

                };
                _self.$api('sendCode', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;
                        console.log("false22:", _self.timer);
                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        console.log("false11:", _self.show);

                        _self.$message({
                            message: _self.$L('邮箱验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {

                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$emit("closeDialog");
                    });
                });


            },
            bindFunc(){
                if (this.newEmail.trim() === '') {
                    this.$message.error(this.$L("请输入邮箱"));
                    return;
                } else if (this.emailCode.trim() === '') {
                    this.$message.error(this.$L("请输入验证码"));
                    return;
                } else {
                    if (this.newEmail.trim() !== '') {
                        let reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
                        if (!reg.test(this.newEmail.trim())) {
                            this.$message.error(this.$L("请输入有效的邮箱"));
                            return;
                        }
                    }

                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    email:　_self.newEmail,
                    code: _self.emailCode,
                };
                _self.$api('bindEmail', arr, function (reData) {
                    if (reData.flag === 1) {

                        _self.$message({
                            message: _self.$L('邮箱绑定成功咯！'),
                            type: 'success'
                        });
                        _self.$emit("closeDialog");
                        _self.reload();
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.data.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            },
            modifyFunc(){
                this.$message({
                    message:'邮箱修改成功咯！',
                    type: 'success'
                });
                this.$emit("closeDialog");
            },
            pwdCheck(){

                if (this.password.trim() === '') {
                    this.$message.error(this.$L("请输入密码"));
                    return;
                }

                const _self = this;
                _self.loading = true;
                let postData = {
                    password: _self.password
                };
                _self.$api('verifyPassword', postData, function (reData) {
                    if (reData.flag === 1) {

                        _self.emailNum = reData.data.email;
                        _self.pass = 1;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });



            },
            phoneCheck(){

                if (this.mobileCode.trim() === '') {
                    this.$message.error(this.$L("请输入验证码"));
                    return;
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    type:　1,
                    code: _self.mobileCode,
                    mobile: _self.data.trueMobileNum,
                    code_type:'sms'
                };
                _self.$api('validationCode', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.timer = false;
                        _self.pass = 1;
                        _self.show = true;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.data.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib";
    .bindEmail{
        position: relative;
        height: 400px;
        .email-form{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 30px;
            text-align: center;
            width: 100%;
            .title{
                font-size: 16px;
            }
            .remind{
                font-size: 12px;
                color: #999999;
                margin-top: 10px;
            }
            .phone-box{
                margin-top: 30px;
                .el-input{
                    width: 300px;
                    margin-bottom: 30px;
                }
                .el-button{
                    font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 300px;
                }
            }
            .safetyCheck{
                position: relative;
                height: 400px;
                .phone-form{
                    top: 30px;
                    text-align: center;
                    width: 100%;
                    position: relative;
                    .el-tabs{
                        width: 300px;
                        margin: 0 auto 60px;
                        font-size: 20px;
                        /deep/ .el-tabs__item{
                            font-size: 20px;
                            @extend .no-select;
                        }
                        /deep/ .el-tabs__nav-wrap:after{
                            height: 0;
                        }
                    }
                    .title{
                        margin-top: 20px;
                        font-size: 16px;
                    }
                    .remind{
                        font-size: 12px;
                        color: #999999;
                        margin-top: 10px;
                    }
                }
            }
            .phone-box{
                margin-top: 30px;
                .el-input{
                    width: 300px;
                    margin-bottom: 30px;
                    /deep/ .el-input-group__append{
                        cursor: pointer;
                        @extend .no-select;
                    }
                }
                .el-button{
                    font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 300px;
                }
                .getMess{
                    line-height: 34px;
                    width: 80px;
                    padding: 0;
                    text-align: center;
                }
            }
        }
    }
</style>