<template>
    <div class="bindPhone">
        <div v-if="data.isset === 0" class="phone-form">
            <p class="title">{{$L('绑定手机后，您可以使用手机号快捷登录')}}</p>
            <div class="phone-box">
                <el-input v-model="phoneNumber" :placeholder="$L('输入手机号')"></el-input>
                <el-input v-model="phoneCode" :placeholder="$L('输入手机验证码')">
                    <el-button @click="getNewPhoneMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>
                    <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>
                </el-input>
                <el-button type="primary" @click="bindFunc">{{ $L(' 绑定') }}</el-button>
            </div>
        </div>

        <div v-else-if="data.isset === 1" class="phone-form">
            <div v-if="pass === 1">
                <p class="title">{{$L('当前手机号')}}：{{ data.mobileNum }}</p>
                <p class="remind">{{$L('更换手机后，下次登录可使用新手机号登录')}}</p>
                <div class="phone-box">
                    <el-input v-model="phoneNumber" :placeholder="$L('输入手机号')"></el-input>
                    <el-input v-model="phoneCode" :placeholder="$L('输入手机验证码')">

                        <el-button @click="getNewPhoneMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>
                        <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>

                    </el-input>
                    <el-button type="primary" @click="bindFunc">{{ $L('提交') }}</el-button>
                </div>
            </div>
            <div class="safetyCheck" v-else-if="pass === 0">
                <el-form class="phone-form">
                    <el-tabs v-model="activeName">
                        <el-tab-pane :label="$L('密码验证')" name="pwd">
                            <div class="phone-box">
                                <el-input size="medium" v-model="password" show-password :placeholder="$L('输入登录密码')"></el-input>
                                <el-button type="primary" @click="pwdCheck">{{ $L('验证') }}</el-button>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane :label="$L('手机验证')" name="phone">
                            <p class="title">{{$L('当前手机号')}}：{{ data.mobileNum }}</p>
                            <div class="phone-box">
                                <el-input v-model="mobileCode" :placeholder="$L('输入手机验证码')">
                                    <el-button @click="getPhoneMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>
                                    <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>
                                </el-input>
                                <el-button type="primary" @click="phoneCheck">{{ $L('验证') }}</el-button>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "bindPhone",
        inject: ["reload"],
        props: {
            data: {
                type:Object,
                default:()=>{
					return {};
				}
            }
        },
        data(){
            return {
                isBind:1,
                pass:0,
                newNumber:'',
                message:'',
                activeName:'pwd',
                type:'phone',
                isPwd:0,
                phoneNumber:'',
                phoneCode:'',
                show: true,
                count: '',
                timer: null,
                tableData: {},
                password: '',
                mobileCode: '',
                newPhoneNum: '',
            }
        },
        created() {
            
        },
        methods:{
            getPhoneMessage(){

                const _self = this;
                _self.loading = true;
                let arr = {
                    send_mobile: 1

                };
                _self.$api('sendCodeChange', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;

                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        console.log("false:", _self.show);

                        _self.$message({
                            message: _self.$L('手机验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {

                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            },
            getNewPhoneMessage(){

                if (this.phoneNumber !== '') {
                    let reg = /^1[0-9]{10}$/;
                    if (!reg.test(this.phoneNumber)) {
                        this.$message.error(this.$L("手机号码不正确"));
                        return;
                    }
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    send_mobile: 1,
                    mobile: _self.phoneNumber,
                };
                _self.$api('sendCode', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;

                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        console.log("false:", _self.show);

                        _self.$message({
                            message: _self.$L('新手机验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            },
            bindFunc(){

                if (this.phoneNumber.trim() === '') {
                    this.$message.error(this.$L("请输入手机号码"));
                    return;
                } else if (this.phoneCode.trim() === '') {
                    this.$message.error(this.$L("请输入验证码"));
                    return;
                } else {
                    if (this.phoneNumber.trim() !== '') {
                        let reg = /^1[0-9]{10}$/;
                        if (!reg.test(this.phoneNumber.trim())) {
                            this.$message.error(this.$L("请输入有效的手机号码"));
                            return;
                        }
                    }

                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    mobile:　_self.phoneNumber,
                    code: _self.phoneCode,
                };
                _self.$api('bindMobile', arr, function (reData) {
                    if (reData.flag === 1) {

                        _self.$message({
                            message: _self.$L('手机绑定成功咯！'),
                            type: 'success'
                        });
                        _self.$emit("closeDialog");
                        _self.reload();
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误



                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });


            },
            hidePhone(phone){
                return phone.substr(0, 3) + '****' + phone.substr(7);
            },
            modifyFunc(){
                this.$message({
                    message:'手机号修改成功咯！',
                    type: 'success'
                })
                this.$emit("closeDialog");
            },
            pwdCheck(){
                if (this.password.trim() === '') {
                    this.$message.error(this.$L("请输入密码"));
                    return;
                }

                const _self = this;
                _self.loading = true;
                let postData = {
                    password: _self.password
                };
                _self.$api('verifyPassword', postData, function (reData) {
                    if (reData.flag === 1) {

                        _self.emailNum = reData.data.email;
                        _self.pass = 1;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });
            },
            phoneCheck(){

                if (this.mobileCode.trim() === '') {
                    this.$message.error(this.$L("请输入验证码"));
                    return;
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    type:　1,
                    code: _self.mobileCode,
                    mobile: _self.data.trueMobileNum,
                    code_type:'sms'
                };
                _self.$api('validationCode', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.timer = false;
                        _self.pass = 1;
                        _self.show = true;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

                this.pass = 1;
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib";
    .bindPhone{
        position: relative;
        height: 400px;
        .phone-form{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 50px;
            text-align: center;
            .title{
                font-size: 16px;
            }
            .remind{
                font-size: 12px;
                color: #999999;
                margin-top: 10px;
            }
            .phone-box{
                margin-top: 30px;
                .el-input{
                    width: 300px;
                    margin-bottom: 30px;
                }
                .el-button{
                     font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 300px;
                }
            }
            .safetyCheck{
                position: relative;
                height: 400px;
                .phone-form{
                    top: 30px;
                    text-align: center;
                    width: 100%;
                    position: relative;
                    .el-tabs{
                        width: 300px;
                        margin: 0 auto 60px;
                        font-size: 20px;
                        /deep/ .el-tabs__item{
                            font-size: 20px;
                            @extend .no-select;
                        }
                        /deep/ .el-tabs__nav-wrap:after{
                            height: 0;
                        }
                    }
                    .title{
                        margin-top: 20px;
                        font-size: 16px;
                    }
                    .remind{
                        font-size: 12px;
                        color: #999999;
                        margin-top: 10px;
                    }
                }
            }
            .phone-box{
                margin-top: 30px;
                .el-input{
                    width: 300px;
                    margin-bottom: 30px;
                    /deep/ .el-input-group__append{
                        cursor: pointer;
                        @extend .no-select;
                    }
                }
                .el-button{
                    font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 300px;
                }
                .getMess{
                    line-height: 34px;
                    width: 80px;
                    padding: 0;
                    text-align: center;
                }
            }
        }
    }
</style>